
#notes{
    width: 100%;
}
.topBar{
    width: 100%;
    height: 1.8rem;
    -webkit-box-shadow:0px 3px 3px #e4e4e4;
            box-shadow:0px 3px 3px #e4e4e4;
    position: fixed;
    top: 0;
    z-index: 1;
    background-color: #fcfcfc;
    overflow: hidden;
}
.topBarPlaceHolder{
    width: 100%;
    height: 1.8rem;
}
.uplift-enter,.uplift-leave-to{
  opacity: 0;
  -webkit-transform: translateY(1.5rem);
          transform: translateY(1.5rem);
}
.uplift-enter-to{
    opacity: 1;
}
.uplift-enter-active,.uplift-leave-active{
    -webkit-transition: all .5s;
    transition: all .5s;
}











