
.note[data-v-60ec407a]{
    display: block;
    height: 2rem;
    margin-top: .3rem;
    font-size: .5rem;
    width: 90%;
    margin: .5rem auto 0rem auto;
    border-radius: 3px;
    color:#505558;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(204, 204,204, .5);
    position: relative;
}

/* .note:active{ */
    /* opacity: 1.2; */

/* } */
.note.yellow[data-v-60ec407a]{
    background-color: #eff1af
}
.note.pink[data-v-60ec407a]{
    background-color: #edd5c5
}
.note.green[data-v-60ec407a]{
    background-color: #bae0be
}
.note.blue[data-v-60ec407a] {
    background-color: #bbd0eb
}
.note .title[data-v-60ec407a],.date[data-v-60ec407a]{
    margin-left: .3rem;
    display: inline-block;
}
.title[data-v-60ec407a]{
    font-size: .5rem;
    margin-top: .5rem;
}
.date[data-v-60ec407a]{
    font-size: .2rem;
}
.del-icon[data-v-60ec407a]{
    font-family: "true";
    position: absolute;
    color: #fff;
    top: 5px;
    right: 5px;
    cursor: pointer;
}
.del-icon[data-v-60ec407a]:active{
    color: #ccc;
}
.del-icon[data-v-60ec407a]::before{
    content: "\ea0f"
}




