
.model{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: #ccc;
    z-index: 5;
    opacity: .3;
    -webkit-transition: all .5s;
    transition: all .5s
}
.model-fade-enter,.model-fade-leave-to{
  opacity: 0;
}
.model-fade-enter-to{
    opacity: .3;
}
.model-fade-enter-active,.model-fade-leave-active{
    -webkit-transition: all .5s;
    transition: all .5s;
}



